import React, { useContext } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { AuthContext } from '../context/AuthContext';

const Navbar = ({ showAdminLink = false, showCoachLink = false }) => {
  const { currentUser, adminUser, coachUser, logout, isAdmin, isCoach } = useContext(AuthContext);
  const navigate = useNavigate();
  
  // Get current path to determine what to display
  const currentPath = window.location.pathname;
  const isCoachPath = currentPath.startsWith('/coach');
  
  const handleLogout = (type) => {
    // First clear the state by calling logout
    logout(type);
    
    // Then navigate after state is cleared
    if (type === 'admin') {
      navigate('/admin/login', { replace: true });
    } else if (type === 'coach') {
      navigate('/coach/login', { replace: true });
    } else {
      navigate('/', { replace: true });
    }
  };  // For client side (home) or coach dashboard
  if (!showAdminLink) {
    return (
      <nav className="navbar navbar-expand-lg" style={{ backgroundColor: 'var(--bg-dark)', color: 'var(--primary-color)' }}>
        <div className="container">
          <Link className="navbar-brand d-flex align-items-center" to="/">
            <img src="/logo_white.png" alt="Suite Coaching Logo" height="40" className="me-2" />
            <span style={{ color: 'var(--primary-color)', fontWeight: 'bold' }}>
              {showCoachLink ? 'Coach Dashboard' : 'Suite Coaching'}
            </span>
          </Link>
          
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarClientNav"
            style={{ backgroundColor: 'var(--secondary-color)' }}
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          
          <div className="collapse navbar-collapse" id="navbarClientNav">
            <ul className="navbar-nav ms-auto">              {/* Show client user dropdown only on client paths when a client user is logged in */}
              {!isCoachPath && !showCoachLink && currentUser && currentUser.role === 'user' ? (
                <li className="nav-item dropdown">
                  <a 
                    className="nav-link dropdown-toggle" 
                    href="#" 
                    id="navbarClientDropdown" 
                    role="button" 
                    data-bs-toggle="dropdown" 
                    aria-expanded="false"
                    style={{ color: 'var(--primary-color)' }}
                  >
                    <i className="fas fa-user-circle me-1"></i>
                    {currentUser.username}
                  </a>
                  <ul className="dropdown-menu dropdown-menu-end" aria-labelledby="navbarClientDropdown" style={{ backgroundColor: 'var(--secondary-color)', borderColor: 'var(--highlight-color)' }}>
                    <li>
                      <span className="dropdown-item-text">
                        <i className="fas fa-envelope me-2"></i>
                        {currentUser.email}
                      </span>
                    </li>
                    <li>
                      <span className="dropdown-item-text">
                        <i className="fas fa-star me-2" style={{ color: 'var(--highlight-color)' }}></i>
                        Points: {currentUser.points || 0}
                        <small className="ms-2" style={{ color: 'var(--highlight-color)' }}>(Updates instantly)</small>
                      </span>
                    </li>
                    <li><hr className="dropdown-divider" style={{ borderColor: 'var(--highlight-color)', opacity: 0.2 }} /></li>
                    <li>
                      <button className="dropdown-item" onClick={() => handleLogout('client')} style={{ color: 'var(--primary-color)' }}>
                        <i className="fas fa-sign-out-alt me-2"></i>
                        Logout
                      </button>
                    </li>
                  </ul>
                </li>
              ) : /* Show coach dropdown only on coach paths when a coach is logged in */ 
              isCoachPath && coachUser && coachUser.role === 'coach' ? (
                <li className="nav-item dropdown">
                  <a 
                    className="nav-link dropdown-toggle" 
                    href="#" 
                    id="navbarCoachDropdown" 
                    role="button" 
                    data-bs-toggle="dropdown" 
                    aria-expanded="false"
                    style={{ color: 'var(--primary-color)' }}
                  >                    <i className="fas fa-user-tie me-1"></i>
                    {coachUser.name || coachUser.username}
                  </a>
                  <ul className="dropdown-menu dropdown-menu-end" aria-labelledby="navbarCoachDropdown" style={{ backgroundColor: 'var(--secondary-color)', borderColor: 'var(--highlight-color)' }}>
                    <li>
                      <span className="dropdown-item-text">
                        <i className="fas fa-id-badge me-2"></i>
                        {coachUser.matricule}
                      </span>
                    </li>
                    <li><hr className="dropdown-divider" style={{ borderColor: 'var(--highlight-color)', opacity: 0.2 }} /></li>
                    <li>
                      <button className="dropdown-item" onClick={() => handleLogout('coach')} style={{ color: 'var(--primary-color)' }}>
                        <i className="fas fa-sign-out-alt me-2"></i>
                        Logout
                      </button>
                    </li>
                  </ul>
                </li>              ) : (                <div className="d-flex">
                  {/* Only show client login on non-coach paths */}
                  {!isCoachPath && (
                    <li className="nav-item me-2">
                      <Link className="nav-link" to="/login" style={{ color: 'var(--primary-color)' }}>
                        <i className="fas fa-sign-in-alt me-1"></i> Client Login
                      </Link>
                    </li>
                  )}
                  
                  {/* Only show coach login link when on coach paths and not already on login page */}
                  {isCoachPath && !currentPath.includes('/coach/login') && (
                    <li className="nav-item">
                      <Link className="nav-link" to="/coach/login" style={{ color: 'var(--primary-color)' }}>
                        <i className="fas fa-user-tie me-1"></i> Coach Login
                      </Link>
                    </li>
                  )}
                </div>
              )}
            </ul>
          </div>
        </div>
      </nav>
    );
  }    // If we're on the admin side, show the full navigation
  return (    <nav className="navbar navbar-expand-lg" style={{ backgroundColor: 'var(--bg-dark)' }}>
      <div className="container">
        <Link className="navbar-brand d-flex align-items-center" to="/admin">
          <img src="/logo_white.png" alt="Suite Coaching Logo" height="40" className="me-2" />
          <span style={{ color: 'var(--primary-color)', fontWeight: 'bold' }}>Suite Coaching Admin</span>
        </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNav"
          style={{ backgroundColor: 'var(--highlight-color)' }}
        >
          <span className="navbar-toggler-icon"></span>
        </button>        <div className="collapse navbar-collapse" id="navbarNav">
          <ul className="navbar-nav ms-auto">
            {/* Show admin dashboard link only to admin users */}
            {isAdmin && (
              <li className="nav-item">
                <Link className="nav-link" to="/admin" style={{ color: '#333333' }}>
                  <i className="fas fa-user-shield me-1"></i> Admin Dashboard
                </Link>
              </li>
            )}

            {/* Show login link to non-authenticated admin users */}
            {!adminUser && (
              <li className="nav-item">
                <Link className="nav-link" to="/admin/login" style={{ color: '#333333' }}>
                  <i className="fas fa-sign-in-alt me-1"></i> Admin Login
                </Link>
              </li>
            )}
              
            {/* User dropdown for authenticated admin users */}
            {adminUser ? (
              <li className="nav-item dropdown">
                <a 
                  className="nav-link dropdown-toggle" 
                  href="#" 
                  id="navbarDropdown" 
                  role="button" 
                  data-bs-toggle="dropdown" 
                  aria-expanded="false"
                  style={{ color: '#333333' }}
                >
                  <i className="fas fa-user-circle me-1"></i>
                  {adminUser.username}
                </a>
                <ul className="dropdown-menu dropdown-menu-end" aria-labelledby="navbarDropdown" style={{ backgroundColor: '#f8f9fa', borderColor: '#0099CC' }}>
                  <li>
                    <span className="dropdown-item-text text-muted">
                      <i className="fas fa-id-card me-2"></i>
                      Matricule: {adminUser.matricule}
                    </span>
                  </li>
                  <li><hr className="dropdown-divider" /></li>
                  {isAdmin && (
                    <li>
                      <Link className="dropdown-item" to="/admin" style={{ color: '#333333' }}>
                        <i className="fas fa-cogs me-2"></i>
                        Admin Panel
                      </Link>
                    </li>
                  )}                  <li>
                    <button className="dropdown-item" onClick={() => handleLogout('admin')} style={{ color: '#333333' }}>
                      <i className="fas fa-sign-out-alt me-2"></i>
                      Logout
                    </button>
                  </li>
                </ul>
              </li>            
            ) : null}
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
