import React, { useState, useEffect } from 'react';

// Global state to store active confirmation dialogs
let confirmCallback = null;
let rejectCallback = null;

// Custom event names
const SHOW_CONFIRM_EVENT = 'show-confirm-dialog';
const HIDE_CONFIRM_EVENT = 'hide-confirm-dialog';

// Function to show a confirmation dialog (to be used instead of window.confirm)
export const showConfirm = (message, options = {}) => {
  return new Promise((resolve, reject) => {
    const { 
      title = 'Confirmation Required', 
      confirmText = 'Confirm', 
      cancelText = 'Cancel',
      type = 'warning'
    } = options;
    
    // Set callbacks
    confirmCallback = () => resolve(true);
    rejectCallback = () => resolve(false);
    
    // Dispatch custom event to show dialog
    const event = new CustomEvent(SHOW_CONFIRM_EVENT, {
      detail: { message, title, confirmText, cancelText, type }
    });
    window.dispatchEvent(event);
  });
};

// Component to handle confirmation dialogs
const ConfirmDialog = () => {
  const [visible, setVisible] = useState(false);
  const [dialogProps, setDialogProps] = useState({
    message: '',
    title: 'Confirmation Required',
    confirmText: 'Confirm',
    cancelText: 'Cancel',
    type: 'warning'
  });

  useEffect(() => {
    // Handler for showing dialog
    const handleShowDialog = (event) => {
      const { message, title, confirmText, cancelText, type } = event.detail;
      setDialogProps({ message, title, confirmText, cancelText, type });
      setVisible(true);
    };

    // Handler for hiding dialog
    const handleHideDialog = () => {
      setVisible(false);
    };

    // Listen for events
    window.addEventListener(SHOW_CONFIRM_EVENT, handleShowDialog);
    window.addEventListener(HIDE_CONFIRM_EVENT, handleHideDialog);

    return () => {
      window.removeEventListener(SHOW_CONFIRM_EVENT, handleShowDialog);
      window.removeEventListener(HIDE_CONFIRM_EVENT, handleHideDialog);
    };
  }, []);

  // Handle confirm button click
  const handleConfirm = () => {
    setVisible(false);
    if (confirmCallback) {
      confirmCallback();
      confirmCallback = null;
      rejectCallback = null;
    }
  };

  // Handle cancel button click
  const handleCancel = () => {
    setVisible(false);
    if (rejectCallback) {
      rejectCallback();
      confirmCallback = null;
      rejectCallback = null;
    }
  };
  
  // Get dialog color based on type
  const getDialogStyles = (type) => {
    switch (type) {
      case 'warning':
        return {
          headerColor: '#FF9800',
          headerBg: '#FFF3E0',
          borderColor: '#FF9800',
          icon: 'fas fa-exclamation-triangle'
        };
      case 'danger':
        return {
          headerColor: '#F44336',
          headerBg: '#FFEBEE',
          borderColor: '#F44336',
          icon: 'fas fa-exclamation-circle'
        };
      case 'info':
        return {          headerColor: 'var(--highlight-color)',
          headerBg: '#1e1e1e',
          borderColor: 'var(--highlight-color)',
          icon: 'fas fa-info-circle'
        };
      case 'success':
        return {
          headerColor: '#4CAF50',
          headerBg: '#E8F5E9',
          borderColor: '#4CAF50',
          icon: 'fas fa-check-circle'
        };
      default:
        return {
          headerColor: '#FF9800',
          headerBg: '#FFF3E0',
          borderColor: '#FF9800',
          icon: 'fas fa-exclamation-triangle'
        };
    }
  };
  
  const styles = getDialogStyles(dialogProps.type);
  
  if (!visible) return null;

  return (
    <div className="confirm-dialog-backdrop" style={{
      position: 'fixed',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      backgroundColor: 'rgba(0, 0, 0, 0.5)',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      zIndex: 9999,
      animation: 'fadeIn 0.2s ease-out'
    }}>
      <div className="confirm-dialog" style={{
        backgroundColor: '#fff',
        borderRadius: '8px',
        boxShadow: '0 4px 20px rgba(0, 0, 0, 0.15)',
        width: '90%',
        maxWidth: '450px',
        overflow: 'hidden',
        animation: 'zoomIn 0.2s ease-out',
        border: `1px solid ${styles.borderColor}`
      }}>
        <div className="confirm-dialog-header" style={{
          padding: '16px 20px',
          backgroundColor: styles.headerBg,
          color: styles.headerColor,
          fontWeight: 'bold',
          fontSize: '1.1rem',
          display: 'flex',
          alignItems: 'center',
          borderBottom: `1px solid ${styles.borderColor}`
        }}>
          <i className={`${styles.icon} me-2`}></i>
          {dialogProps.title}
        </div>
        
        <div className="confirm-dialog-body" style={{
          padding: '20px',
          fontSize: '1rem',
          lineHeight: '1.5'
        }}>
          {dialogProps.message}
        </div>
        
        <div className="confirm-dialog-footer" style={{
          padding: '12px 20px',
          display: 'flex',
          justifyContent: 'flex-end',
          gap: '10px',
          borderTop: '1px solid #eee'
        }}>
          <button 
            className="btn btn-outline-secondary" 
            onClick={handleCancel}
            style={{
              fontSize: '0.9rem',
              fontWeight: '500'
            }}
          >
            {dialogProps.cancelText}
          </button>
          <button 
            className={`btn ${dialogProps.type === 'danger' ? 'btn-danger' : 
                           dialogProps.type === 'success' ? 'btn-success' : 
                           dialogProps.type === 'info' ? 'btn-info' : 'btn-warning'}`}
            onClick={handleConfirm}
            style={{
              fontSize: '0.9rem',
              fontWeight: '500'
            }}
          >
            {dialogProps.confirmText}
          </button>
        </div>
      </div>
    </div>
  );
};

export default ConfirmDialog;
