import React, { useState, useEffect, useRef, useContext } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import axios from 'axios';
import CoachCard from '../components/CoachCard';
import ReservationForm from '../components/ReservationForm';
import { AuthContext } from '../context/AuthContext';

const Home = () => {
  const [coaches, setCoaches] = useState([]);
  const [selectedCoach, setSelectedCoach] = useState(null);
  const [loading, setLoading] = useState(true);
  const videoRef = useRef(null);
  const { currentUser, isUser, logout } = useContext(AuthContext);
  const navigate = useNavigate();
    // Add state for points animation
  const [pointsUpdated, setPointsUpdated] = useState(false);    // Handler for logout action
  const handleLogout = (type) => {
    // First clear the state by calling logout
    logout(type);
    
    // Then navigate after state is cleared - use replace: true to prevent going back to the logged-in state
    navigate('/', { replace: true });
  };
  
  // Check if points have been updated by comparing with localStorage
  useEffect(() => {
    if (currentUser && isUser) {
      const storedUser = JSON.parse(localStorage.getItem('clientUser') || '{}');
      // If the points in context are different from the ones in localStorage, animate
      if (storedUser.points !== currentUser.points) {
        setPointsUpdated(true);
        setTimeout(() => setPointsUpdated(false), 3000); // Reset after 3 seconds
      }
    }
  }, [currentUser?.points, isUser, currentUser]);
  
  // Handle scroll to coaches section
  const scrollToCoaches = () => {
    document.getElementById('coaches-section').scrollIntoView({ 
      behavior: 'smooth' 
    });
  };
  useEffect(() => {
    fetchCoaches();
    
    // Auto-play video when component mounts
    if (videoRef.current) {
      videoRef.current.play().catch(error => {
        console.log('Video autoplay was prevented:', error);
      });
    }
    
    // Add scroll effect for the header
    const handleScroll = () => {
      const header = document.querySelector('.modern-header');
      if (header) {
        if (window.scrollY > 50) {
          header.classList.add('scrolled');
        } else {
          header.classList.remove('scrolled');
        }
      }
    };
    
    window.addEventListener('scroll', handleScroll);
    
    // Cleanup
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const fetchCoaches = async () => {
    try {      console.log('Fetching coaches from /api/coaches...');
      const response = await axios.get('/api/coaches');
      console.log('Coaches fetched successfully:', response.data);
      setCoaches(response.data);
    } catch (error) {
      console.error('Error fetching coaches:', error.response?.data || error.message);
      setCoaches([]);
    } finally {
      setLoading(false);
    }
  };

  const handleReserve = (coach) => {
    setSelectedCoach(coach);
  };

  const handleCloseForm = () => {
    setSelectedCoach(null);
  };

  const handleReservationSuccess = () => {
    // Optionally refresh coaches or show success message
    fetchCoaches();
  };

  if (loading) {
    return (
      <div className="container mt-5">
        <div className="spinner-container">
          <div className="spinner-border" role="status">            <span className="visually-hidden">Chargement...</span>
          </div>
          <p className="mt-3">Chargement des coachs...</p>
        </div>
      </div>
    );
  }
  return (
    <div className="main-wrapper">      {/* Modern Header */}
      <header className="modern-header">
        <div className="container">
          <div className="d-flex justify-content-between align-items-center">
            <div className="brand">
              <Link to="/" className="brand-link">
                <img src="/logo_white.png" alt="Suite Coaching" className="brand-logo" />
                <span className="brand-text">Suite Coaching</span>
              </Link>
            </div>
            <div className="user-profile">
              {currentUser ? (
                <div className="dropdown">
                  <button className="btn dropdown-toggle user-button" type="button" id="userDropdown" data-bs-toggle="dropdown" aria-expanded="false">
                    <span className="user-avatar me-2">
                      <i className="fas fa-user-circle"></i>
                    </span>
                    <span className="user-name">{currentUser.username}</span>
                  </button>
                  <ul className="dropdown-menu dropdown-menu-end" aria-labelledby="userDropdown">                    <li><a className="dropdown-item" href="#profile"><i className="fas fa-id-card me-2"></i>Profil</a></li>
                    <li><a className="dropdown-item" href="#settings"><i className="fas fa-cog me-2"></i>Paramtres</a></li>
                    <li><hr className="dropdown-divider" /></li>
                    <li><button className="dropdown-item" onClick={() => handleLogout('user')}><i className="fas fa-sign-out-alt me-2"></i>Dconnexion</button></li>
                  </ul>
                </div>
              ) : (              <Link to="/login" className="btn login-button">
                  <i className="fas fa-sign-in-alt me-2"></i>
                  Connexion
                </Link>
              )}
            </div>
          </div>
        </div>
      </header>
        {/* Professional Video Hero Section */}
      <div className="video-hero-section">
        <video 
          ref={videoRef}
          className="video-background fullscreen-video" 
          loop 
          muted 
          autoPlay
          playsInline
        >
          <source src="/Video.mp4" type="video/mp4" />
          Your browser does not support the video tag.
        </video>
        <div className="video-overlay">
          {/* Points display for logged-in users */}
          {currentUser && isUser && (
            <div className={`points-display ${pointsUpdated ? 'points-updated' : ''}`}>
              <i className="fas fa-star me-2"></i>              <strong>Vos Crdits: {currentUser.points || 0}</strong>
              {pointsUpdated && (
                <span className="ms-2 points-updated-badge">
                  Mis  jour!
                </span>
              )}
            </div>
          )}          <div className="hero-content text-center">
            <h1 className="hero-title">Transformez Votre <span className="highlight">Potentiel</span></h1>
            <p className="hero-subtitle">Dcouvrez un coaching personnalis qui lve vos performances</p>
            <button 
              className="btn-book-now"
              onClick={() => scrollToCoaches()}
            >
              <span className="btn-text">Rservez une Sance</span>
              <span className="btn-icon"><i className="fas fa-arrow-right"></i></span>            </button>
            
            <div className="sound-control">
              <button 
                className="btn-sound sound-icon-transition" 
                onClick={() => {
                  if (videoRef.current) {
                    if (videoRef.current.muted) {
                      videoRef.current.muted = false;
                      document.querySelector('.sound-control i').className = 'fas fa-volume-up';
                    } else {
                      videoRef.current.muted = true;
                      document.querySelector('.sound-control i').className = 'fas fa-volume-mute';
                    }
                  }
                }}
              >
                <i className="fas fa-volume-mute"></i>
              </button>
            </div>
            
            {/* Floating arrow to indicate scrolling */}
            <div className="scroll-arrow" onClick={() => scrollToCoaches()}>
              <i className="fas fa-chevron-down"></i>
            </div>
          </div>
        </div>
      </div>
        {/* Coaches Section */}      <section className="premium-section">
        <div id="coaches-section" className="container slide-up">
          <div className="page-header">            <h2 className="display-4 mb-3" style={{ color: 'var(--secondary-color)' }}>Services de Coaching Professionnel</h2>
            <div className="section-separator"></div>
            <p className="lead" style={{ color: 'var(--text-color)' }}>
              Connectez-vous avec nos coachs expriments pour atteindre vos objectifs personnels et professionnels
            </p>
          
          {isUser && currentUser && (              <div className="client-points-display mb-4">
              <div className="card shadow-sm border-0" style={{ background: 'rgba(30, 30, 30, 0.03)' }}>
                <div className="card-body text-center">
                  <h5 className="mb-0">
                    <i className="fas fa-star me-2" style={{ color: 'var(--highlight-color)' }}></i>
                    Vos Crdits: <span className="badge bg-primary">{currentUser.points || 0}</span>
                  </h5>
                  <p className="small mt-2 mb-0" style={{ color: 'var(--text-color)' }}>Gagnez des crdits  chaque sance de coaching!</p>
                </div>
              </div>
            </div>
          )}
  
        </div>

        <div className="row">
          {coaches.map(coach => (
            <CoachCard
              key={coach.id}
              coach={coach}
              onReserve={handleReserve}
            />
          ))}
        </div>        {coaches.length === 0 && (          <div className="text-center mt-5 p-5 rounded-3 shadow-sm" style={{ background: 'rgba(30, 30, 30, 0.05)' }}>
            <i className="fas fa-user-slash fa-3x mb-3" style={{ color: 'var(--highlight-color)' }}></i>
            <h3 style={{ color: 'var(--secondary-color)' }}>Aucun coach disponible pour le moment</h3>
            <p style={{ color: 'var(--text-color)', opacity: 0.7 }}>Veuillez revenir plus tard ou nous contacter pour plus d'informations.</p>
          </div>
        )}

        {selectedCoach && (
          <ReservationForm
            coach={selectedCoach}
            onClose={handleCloseForm}
            onSuccess={handleReservationSuccess}
          />        )}
      </div>
      </section>
    </div>
  );
};

export default Home;
