import React, { useState, useContext, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { AuthContext } from '../context/AuthContext';

const Registration = ({ coach, onClose, onRegisterSuccess, initialData = {} }) => {
  const { register } = useContext(AuthContext);
  const navigate = useNavigate();
  
  const [formData, setFormData] = useState({
    username: '',
    email: initialData.email || '',
    password: '',
    confirmPassword: '',
    full_name: initialData.full_name || '',
    phone: initialData.phone || '',
    age: initialData.age || '',
    gender: initialData.gender || '',
    goal: initialData.goal || ''
  });
  
  // Update form data if initialData changes
  useEffect(() => {
    setFormData(prevData => ({
      ...prevData,
      email: initialData.email || prevData.email,
      full_name: initialData.full_name || prevData.full_name,
      phone: initialData.phone || prevData.phone,
      age: initialData.age || prevData.age,
      gender: initialData.gender || prevData.gender,
      goal: initialData.goal || prevData.goal
    }));
  }, [initialData]);
  
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };
  
  const validateForm = () => {      if (formData.password !== formData.confirmPassword) {
      setError('Les mots de passe ne correspondent pas');
      return false;
    }
    
    if (formData.password.length < 6) {
      setError('Le mot de passe doit comporter au moins 6 caractres');
      return false;
    }
    
    if (!formData.email.includes('@')) {
      setError('Veuillez entrer une adresse e-mail valide');
      return false;
    }
    
    return true;
  };  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    
    if (!validateForm()) return;
    
    setLoading(true);
    
    try {
      // Remove confirmPassword before sending to API
      const { confirmPassword, ...registrationData } = formData;
      
      console.log('Submitting registration data:', registrationData);
      
      // Use the register function from AuthContext
      const user = await register(registrationData);
      
      console.log('Registration successful:', user);
      
      // Call the success callback with the user data
      if (onRegisterSuccess) {
        onRegisterSuccess(user);
      }
      
    } catch (error) {
      console.error('Registration error:', error);
      const errorMessage = error.response?.data?.error || 'chec de l\'inscription. Veuillez ressayer.';
      console.log('Error details:', errorMessage);
      setError(errorMessage);
    } finally {
      setLoading(false);
    }  };
  
  return (
    <>
      <div className="modal-backdrop" style={{ position: 'fixed', top: 0, left: 0, width: '100%', height: '100%', backgroundColor: 'rgba(0,0,0,0.85)', zIndex: 10000 }}></div>
      <div className="modal show d-block" tabIndex="-1" style={{ position: 'fixed', top: 0, left: 0, width: '100%', height: '100%', zIndex: 10001, paddingTop: '30px', overflow: 'auto' }}>
        <div className="modal-dialog modal-lg" style={{ marginTop: '50px', position: 'relative' }}>
        <div className="modal-content">
          <div className="modal-header">            <h5 className="modal-title">
              <i className="fas fa-user-plus me-2"></i>
              S'inscrire pour rserver avec {coach?.name}
            </h5>
            <button type="button" className="btn-close" onClick={onClose}></button>
          </div>
          
          <div className="modal-body">
            {error && (
              <div className="alert alert-danger" role="alert">
                <i className="fas fa-exclamation-triangle me-2"></i>
                {error}
              </div>
            )}
              <p className="text-muted mb-4">
              Crez un compte pour rserver facilement des sances avec nos coachs. Vos informations seront enregistres pour les rservations futures.
            </p>
            
            <form onSubmit={handleSubmit}>
              <div className="row">
                <div className="col-md-6">                  <h6 className="mb-3">Informations du compte</h6>
                  
                  <div className="mb-3">
                    <label htmlFor="username" className="form-label">Nom d'utilisateur</label>
                    <input
                      type="text"
                      className="form-control"
                      id="username"
                      name="username"
                      value={formData.username}
                      onChange={handleChange}
                      required
                    />
                  </div>
                  
                  <div className="mb-3">
                    <label htmlFor="email" className="form-label">Email</label>
                    <input
                      type="email"
                      className="form-control"
                      id="email"
                      name="email"
                      value={formData.email}
                      onChange={handleChange}
                      required
                    />
                  </div>
                    <div className="mb-3">
                    <label htmlFor="password" className="form-label">Mot de passe</label>
                    <input
                      type="password"
                      className="form-control"
                      id="password"
                      name="password"
                      value={formData.password}
                      onChange={handleChange}
                      required
                    />
                  </div>
                    <div className="mb-3">
                    <label htmlFor="confirmPassword" className="form-label">Confirmer le mot de passe</label>
                    <input
                      type="password"
                      className="form-control"
                      id="confirmPassword"
                      name="confirmPassword"
                      value={formData.confirmPassword}
                      onChange={handleChange}
                      required
                    />
                  </div>
                </div>
                  <div className="col-md-6">
                  <h6 className="mb-3">Informations personnelles</h6>
                  
                  <div className="mb-3">
                    <label htmlFor="full_name" className="form-label">Nom complet</label>
                    <input
                      type="text"
                      className="form-control"
                      id="full_name"
                      name="full_name"
                      value={formData.full_name}
                      onChange={handleChange}
                      required
                    />
                  </div>
                    <div className="mb-3">
                    <label htmlFor="phone" className="form-label">Numro de tlphone</label>
                    <input
                      type="tel"
                      className="form-control"
                      id="phone"
                      name="phone"
                      value={formData.phone}
                      onChange={handleChange}
                      required
                    />
                  </div>
                  
                  <div className="row">
                    <div className="col-md-6">
                      <div className="mb-3">
                        <label htmlFor="age" className="form-label">ge</label>
                        <input
                          type="number"
                          className="form-control"
                          id="age"
                          name="age"
                          value={formData.age}
                          onChange={handleChange}
                          min="1"
                          max="120"
                          required
                        />
                      </div>
                    </div>
                    <div className="col-md-6">
                      <div className="mb-3">                        <label htmlFor="gender" className="form-label">Genre</label>
                        <select
                          className="form-select"
                          id="gender"
                          name="gender"
                          value={formData.gender}
                          onChange={handleChange}
                          required
                        >
                          <option value="">Slectionner</option>
                          <option value="male">Homme</option>
                          <option value="female">Femme</option>
                          <option value="other">Autre</option>
                        </select>
                      </div>
                    </div>
                  </div>
                    <div className="mb-3">
                    <label htmlFor="goal" className="form-label">Quels sont vos objectifs?</label>
                    <textarea
                      className="form-control"
                      id="goal"
                      name="goal"
                      rows="3"
                      value={formData.goal}
                      onChange={handleChange}
                    ></textarea>
                  </div>
                </div>
              </div>
              
              <div className="mt-4 text-center">
                <button type="submit" className="btn btn-primary px-5" disabled={loading}>
                  {loading ? (
                    <>
                      <span className="spinner-border spinner-border-sm me-2" role="status" aria-hidden="true"></span>                      Inscription en cours...
                    </>
                  ) : (
                    <>
                      <i className="fas fa-user-plus me-2"></i>
                      S'inscrire et continuer
                    </>
                  )}
                </button>
              </div>
              
              <div className="mt-3 text-center">                <p className="mb-0">
                  Vous avez dj un compte? <a href="#" onClick={(e) => {
                    e.preventDefault();
                    navigate('/client/login');
                  }}>Connectez-vous ici</a>
                </p>
              </div>
            </form>
          </div>
        </div>      </div>
    </div>
    </>
  );
};

export default Registration;
