import React, { createContext, useState, useEffect } from 'react';
import axios from 'axios';

export const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [currentUser, setCurrentUser] = useState(null);
  const [adminUser, setAdminUser] = useState(null);
  const [coachUser, setCoachUser] = useState(null);
  const [loading, setLoading] = useState(true);  useEffect(() => {
    const initAuth = async () => {
      try {
        // Get the current path
        const currentPath = window.location.pathname;
        const isCoachPath = currentPath.startsWith('/coach');
        const isAdminPath = currentPath.startsWith('/admin');
        
        // Load client user
        const clientToken = localStorage.getItem('clientToken');
        const clientUser = JSON.parse(localStorage.getItem('clientUser') || 'null');
        
        // Load admin user
        const adminToken = localStorage.getItem('adminToken');
        const adminUserData = JSON.parse(localStorage.getItem('adminUser') || 'null');
        
        // Load coach user
        const coachToken = localStorage.getItem('coachToken');
        const coachUserData = JSON.parse(localStorage.getItem('coachUser') || 'null');
        
        // Reset all states first to prevent leaking between sections
        setCurrentUser(null);
        setCoachUser(null);
        
        // Clear any existing authorization headers
        delete axios.defaults.headers.common['Authorization'];
        
        // Set appropriate user state based on path
        if (isCoachPath) {
          // Only on coach routes
          if (coachToken && coachUserData) {
            setCoachUser({...coachUserData, role: 'coach'});
            // Only set currentUser on coach routes
            setCurrentUser({...coachUserData, role: 'coach'});
            axios.defaults.headers.common['Authorization'] = `Bearer ${coachToken}`;
          }
          
          // Explicitly NOT setting client user on coach paths to maintain separation
        } else if (isAdminPath) {
          // Only on admin routes
          if (adminToken && adminUserData) {
            setAdminUser(adminUserData);
            axios.defaults.headers.common['Authorization'] = `Bearer ${adminToken}`;
          } else {
            setAdminUser(null);
          }
        } else {
          // Client/home paths - ONLY use client login here
          if (clientToken && clientUser) {
            setCurrentUser(clientUser);
            axios.defaults.headers.common['Authorization'] = `Bearer ${clientToken}`;
          } else {
            // Make sure currentUser is definitely null if no client token exists
            setCurrentUser(null);
          }
          
          // Admin can be used on client side
          if (adminToken && adminUserData) {
            setAdminUser(adminUserData);
          }
          
          // ENSURE coach user is NEVER set on client paths
          setCoachUser(null);
        }
      } catch (error) {
        console.error("Error initializing auth context:", error);
        // Reset storage if there's a critical error
        localStorage.removeItem('clientToken');
        localStorage.removeItem('clientUser');
        localStorage.removeItem('adminToken');
        localStorage.removeItem('adminUser');
        localStorage.removeItem('lastLoginType');
      } finally {
        setLoading(false);
      }
    };
    
    initAuth();
  }, []);
  
  const login = async (matricule, password) => {
    try {
      const response = await axios.post('/api/auth/login', { matricule, password });
      const { token, userId, role, points, username, matricule: userMatricule } = response.data;
      
      if (role !== 'admin') {
        throw new Error('This login is for admins only');
      }
      
      const user = {
        id: userId,
        username,
        matricule: userMatricule,
        role,
        points: points || 0
      };
      
      // Save as admin
      localStorage.setItem('adminToken', token);
      localStorage.setItem('adminUser', JSON.stringify(user));
      localStorage.setItem('lastLoginType', 'admin');
      
      // Set axios default headers for admin requests
      axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
      
      setAdminUser(user);
      return user;
    } catch (error) {
      console.error("Admin login error:", error);
      throw error;
    }
  };
  
  // Add this function to handle registration
  const register = async (userData) => {
    try {
      const response = await axios.post('/api/auth/register', userData);
      const { token, user } = response.data;
      
      // Save as client user
      localStorage.setItem('clientToken', token);
      localStorage.setItem('clientUser', JSON.stringify(user));
      localStorage.setItem('lastLoginType', 'client');
      
      // Set axios default headers for client requests
      axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
      
      setCurrentUser(user);
      return user;
    } catch (error) {
      console.error("Registration error:", error);
      throw error;
    }
  };
  
  // Add client login function
  const clientLogin = async (email, password) => {
    try {
      const response = await axios.post('/api/auth/client-login', { email, password });
      const { 
        token, 
        userId, 
        role, 
        points, 
        username,
        matricule,
        full_name, 
        email: userEmail, 
        phone, 
        age, 
        gender, 
        goal 
      } = response.data;
      
      const user = {
        id: userId,
        username,
        matricule,
        email: userEmail,
        role,
        points: points || 0,
        full_name,
        phone,
        age,
        gender,
        goal
      };
      
      // Save as client user
      localStorage.setItem('clientToken', token);
      localStorage.setItem('clientUser', JSON.stringify(user));
      localStorage.setItem('lastLoginType', 'client');
      
      // Set axios default headers for client requests
      axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
      
      setCurrentUser(user);
      return user;
    } catch (error) {
      console.error("Client login error:", error);
      throw error;
    }
  };  // Add coach login function
  const coachLogin = async (matricule, password) => {
    try {
      const response = await axios.post('/api/auth/coach-login', { matricule, password });
      const { token, ...userData } = response.data;
      
      // Save as coach user
      localStorage.setItem('coachToken', token);
      localStorage.setItem('coachUser', JSON.stringify({...userData, role: 'coach'}));
      
      // Set axios default headers for coach requests
      axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
      
      // Update only coach specific state
      const coachData = {...userData, role: 'coach'};
      setCoachUser(coachData);
      
      // Only set currentUser if we're on a coach path
      if (window.location.pathname.startsWith('/coach')) {
        setCurrentUser(coachData);
      } else {
        // If not on coach path, make sure currentUser isn't set as coach
        setCurrentUser(null);
      }
      
      return userData;
    } catch (error) {
      console.error("Coach login error:", error);
      throw error;
    }
  };  const logout = (type = 'all') => {
    console.log('Logging out:', type);
    
    // Always clear the authorization headers first to prevent issues
    delete axios.defaults.headers.common['Authorization'];
    
    if (type === 'all' || type === 'client' || type === 'user') {
      localStorage.removeItem('clientToken');
      localStorage.removeItem('clientUser');
      // Always reset currentUser when logging out as client
      setCurrentUser(null);
      console.log('Client/user logged out');
    }
    
    if (type === 'all' || type === 'coach') {
      localStorage.removeItem('coachToken');
      localStorage.removeItem('coachUser');
      setCoachUser(null);
      
      // If on coach path, also reset currentUser
      if (window.location.pathname.startsWith('/coach')) {
        setCurrentUser(null);
      }
      console.log('Coach logged out');
    }
    
    if (type === 'all' || type === 'admin') {
      localStorage.removeItem('adminToken');
      localStorage.removeItem('adminUser');
      setAdminUser(null);
      
      // If on admin path, also reset currentUser
      if (window.location.pathname.startsWith('/admin')) {
        setCurrentUser(null);
      }
      console.log('Admin logged out');
    }
    
    // After logout, set current auth headers based on remaining valid token (if any)
    // This ensures we're using the correct token for the current path
    const currentPath = window.location.pathname;
    if (currentPath.startsWith('/coach')) {
      const coachToken = localStorage.getItem('coachToken');
      if (coachToken) {
        axios.defaults.headers.common['Authorization'] = `Bearer ${coachToken}`;
      }
    } else if (currentPath.startsWith('/admin')) {
      const adminToken = localStorage.getItem('adminToken');
      if (adminToken) {
        axios.defaults.headers.common['Authorization'] = `Bearer ${adminToken}`;
      }
    } else {
      const clientToken = localStorage.getItem('clientToken');
      if (clientToken) {
        axios.defaults.headers.common['Authorization'] = `Bearer ${clientToken}`;
      }
    }
        
    console.log('Logout completed');
  };
    // Add updatePoints function
  const updatePoints = (updatedUser) => {
    console.log('Updating points for user:', updatedUser);
    
    if (updatedUser.role === 'admin') {
      console.log('Updating admin user points from', adminUser?.points, 'to', updatedUser.points);
      setAdminUser(updatedUser);
      localStorage.setItem('adminUser', JSON.stringify(updatedUser));
    } else {
      console.log('Updating client user points from', currentUser?.points, 'to', updatedUser.points);
      setCurrentUser(prevUser => ({
        ...prevUser,
        points: updatedUser.points
      }));
      
      // Update localStorage with the latest user data
      const storedUser = JSON.parse(localStorage.getItem('clientUser') || '{}');
      const updatedStoredUser = { ...storedUser, points: updatedUser.points };
      localStorage.setItem('clientUser', JSON.stringify(updatedStoredUser));
      
      // Force a re-render in components that use this context
      console.log('Points updated successfully');
    }
    
    // Dispatch a custom event that components can listen for
    window.dispatchEvent(new CustomEvent('pointsUpdated', { 
      detail: { 
        userType: updatedUser.role === 'admin' ? 'admin' : 'client',
        points: updatedUser.points
      } 
    }));
  };
    // Add to global window object for access from components
  window.updateUserPoints = updatePoints;const value = {
    currentUser,
    adminUser,
    coachUser,
    setCurrentUser,
    setAdminUser,
    setCoachUser,
    loading,
    login,
    logout,
    register,
    clientLogin,
    coachLogin,
    updatePoints,
    isAdmin: adminUser?.role === 'admin',
    isCoach: window.location.pathname.startsWith('/coach') && coachUser?.role === 'coach',
    // Only consider user to be logged in on client paths when there's a client token and user info
    isUser: !window.location.pathname.startsWith('/coach') && 
            !window.location.pathname.startsWith('/admin') && 
            localStorage.getItem('clientToken') &&
            currentUser?.role === 'user',
  };
  
  return (
    <AuthContext.Provider value={value}>
      {!loading && children}
    </AuthContext.Provider>
  );
};

export default AuthProvider;
