import React, { useState, useEffect, useContext } from 'react';
import { AuthContext } from '../context/AuthContext';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { ToastContext } from '../context/ToastContext';
import { format } from 'date-fns';

const CoachDashboard = () => {
  const { coachUser, logout } = useContext(AuthContext);
  const { showToast } = useContext(ToastContext);
  const navigate = useNavigate();
  
  // Ensure we're on coach path and redirect if no coach token
  useEffect(() => {
    // Check if coach is logged in
    const coachToken = localStorage.getItem('coachToken');
    if (!coachToken) {
      navigate('/coach/login', { replace: true });
      return;
    }
    
    // Fetch data when component mounts
    fetchCoachData();
  }, [navigate, coachUser]);
  
  const [reservations, setReservations] = useState([]);
  const [availability, setAvailability] = useState([]);
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState('reservations');
  const [error, setError] = useState(null);
  
  // Form state for adding availability
  const [newAvailability, setNewAvailability] = useState({
    date: '',
    startTime: '',
    endTime: ''
  });
  
  // Profile state
  const [profile, setProfile] = useState({
    name: '',
    email: '',
    bio: '',
    specialty: '',
    photo: ''
  });
    const fetchCoachData = async () => {
    setLoading(true);
    setError(null);
    try {
      console.log('Fetching coach data...');
      
      // Fetch coach profile
      const profileRes = await axios.get('/api/coach-dashboard/profile');
      setProfile(profileRes.data);
      console.log('Profile loaded:', profileRes.data);
      
      // Fetch reservations
      const reservationsRes = await axios.get('/api/coach-dashboard/reservations');
      setReservations(reservationsRes.data);
      console.log('Reservations loaded:', reservationsRes.data.length);
      
      // Fetch availability
      const availabilityRes = await axios.get('/api/coach-dashboard/availability');
      setAvailability(availabilityRes.data);
      console.log('Availability loaded:', availabilityRes.data.length);
      
    } catch (error) {
      console.error('Error loading coach data:', error);      setError('Failed to load data. Please try again later.');
      showToast('error', 'Failed to load data. Please try again later.');
      
      // Reset states on error
      setProfile({});
      setReservations([]);
      setAvailability([]);
    } finally {
      setLoading(false);
    }
  };
  
  const handleLogout = () => {
    // Clear coach token and state
    logout('coach');
    
    // Force a page reload to reset all states
    window.location.href = '/coach/login';
    
    // Show success toast
    showToast('success', 'Successfully logged out');
  };
  
  const handleTabChange = (tab) => {
    setActiveTab(tab);
  };
  
  const handleAddAvailability = async (e) => {
    e.preventDefault();
    
    try {
      const { date, startTime, endTime } = newAvailability;
      
      // Validate input
      if (!date || !startTime || !endTime) {
        showToast('error', 'Please fill in all availability fields');
        return;
      }
      
      // Add availability
      await axios.post('/api/coach-dashboard/availability', {
        date,
        start_time: startTime,
        end_time: endTime
      });
      
      // Clear form
      setNewAvailability({
        date: '',
        startTime: '',
        endTime: ''
      });
      
      // Refresh availability data
      const availabilityRes = await axios.get('/api/coach-dashboard/availability');
      setAvailability(availabilityRes.data);
      
      showToast('success', 'New availability added successfully');
    } catch (error) {
      console.error('Error adding availability:', error);
      showToast('error', 'Failed to add availability. Please try again.');
    }
  };
  
  const handleRemoveAvailability = async (availabilityId) => {
    try {
      // Remove availability
      await axios.delete(`/api/coach-dashboard/availability/${availabilityId}`);
      
      // Refresh availability data
      const availabilityRes = await axios.get('/api/coach-dashboard/availability');
      setAvailability(availabilityRes.data);
      
      showToast('success', 'Availability removed successfully');
    } catch (error) {
      console.error('Error removing availability:', error);
      showToast('error', 'Failed to remove availability. Please try again.');
    }
  };
  
  const handleUpdateProfile = async (e) => {
    e.preventDefault();
    
    try {
      const { bio, specialty } = profile;
      
      // Update profile
      await axios.put('/api/coach-dashboard/profile', {
        bio,
        specialty
      });
      
      showToast('success', 'Profile updated successfully');
    } catch (error) {
      console.error('Error updating profile:', error);
      showToast('error', 'Failed to update profile. Please try again.');
    }
  };
  
  const formatDate = (dateString) => {
    return format(new Date(dateString), 'PPP');
  };
  
  const formatTime = (timeString) => {
    return format(new Date(`2000-01-01T${timeString}`), 'h:mm a');
  };
    if (loading) {
    return (
      <div className="container mt-5 text-center">
        <div className="spinner-border" role="status">
          <span className="visually-hidden">Loading...</span>
        </div>
        <p className="mt-3">Loading coach dashboard...</p>
      </div>
    );
  }
  
  if (error) {
    return (
      <div className="container mt-5">
        <div className="alert alert-danger">
          <h4>Error Loading Dashboard</h4>
          <p>{error}</p>
          <button className="btn btn-primary mt-2" onClick={fetchCoachData}>
            Retry
          </button>
        </div>
      </div>
    );
  }
  
  return (
    <div className="container mt-4 mb-5">
      <div className="row">
        {/* Coach Profile Sidebar */}
        <div className="col-lg-3">
          <div className="card shadow-sm mb-4">
            <div className="card-body text-center">
              <div className="mb-3">
                <img 
                  src={profile.photo || "https://via.placeholder.com/150x150?text=Coach"} 
                  alt={profile.name} 
                  className="rounded-circle img-thumbnail" 
                  style={{ width: '120px', height: '120px', objectFit: 'cover' }}
                />
              </div>
              <h5 className="card-title fw-bold mb-0">{profile.name}</h5>
              <p className="text-muted mb-2">{profile.specialty}</p>
              <div className="d-grid gap-2 mt-3">
                <button 
                  className="btn btn-outline-danger btn-sm"
                  onClick={handleLogout}
                >
                  <i className="fas fa-sign-out-alt me-2"></i>Logout
                </button>
              </div>
              <p className="text-muted small mb-3">
                <i className="fas fa-envelope me-1"></i> {profile.email}
              </p>
              <div className="d-grid">
                <button 
                  className="btn btn-sm admin-btn-primary"
                  onClick={() => handleTabChange('profile')}
                >
                  <i className="fas fa-user-edit me-1"></i> Edit Profile
                </button>
              </div>
            </div>
          </div>
          
          <div className="card shadow-sm mb-4">
            <div className="card-header bg-dark text-white">
              <h5 className="card-title mb-0">
                <i className="fas fa-tachometer-alt me-2"></i> Dashboard
              </h5>
            </div>
            <div className="list-group list-group-flush">
              <button 
                className={`list-group-item list-group-item-action ${activeTab === 'reservations' ? 'active' : ''}`}
                onClick={() => handleTabChange('reservations')}
              >
                <i className="fas fa-calendar-check me-2"></i> My Reservations 
                <span className="badge bg-secondary rounded-pill float-end">{reservations.length}</span>
              </button>
              <button 
                className={`list-group-item list-group-item-action ${activeTab === 'availability' ? 'active' : ''}`}
                onClick={() => handleTabChange('availability')}
              >
                <i className="fas fa-clock me-2"></i> My Availability
                <span className="badge bg-secondary rounded-pill float-end">{availability.length}</span>
              </button>
              <button 
                className={`list-group-item list-group-item-action ${activeTab === 'profile' ? 'active' : ''}`}
                onClick={() => handleTabChange('profile')}
              >
                <i className="fas fa-user-cog me-2"></i> Profile Settings
              </button>
            </div>
          </div>
        </div>
        
        {/* Main Content */}
        <div className="col-lg-9">
          {/* Reservations Tab */}
          {activeTab === 'reservations' && (
            <div className="card shadow-sm">
              <div className="card-header bg-dark text-white d-flex align-items-center justify-content-between">
                <h5 className="mb-0">
                  <i className="fas fa-calendar-check me-2"></i> My Reservations
                </h5>
                <button className="btn btn-sm btn-outline-light" onClick={() => fetchCoachData()}>
                  <i className="fas fa-sync-alt me-1"></i> Refresh
                </button>
              </div>
              <div className="card-body">
                {reservations.length > 0 ? (
                  <div className="table-responsive">
                    <table className="table table-hover">
                      <thead className="table-light">
                        <tr>
                          <th>Date</th>
                          <th>Time</th>
                          <th>Client Name</th>
                          <th>Contact Info</th>
                          <th>Goal</th>
                        </tr>
                      </thead>
                      <tbody>
                        {reservations.map(reservation => (
                          <tr key={reservation.id}>
                            <td>{formatDate(reservation.date)}</td>
                            <td>{formatTime(reservation.time)}</td>
                            <td>
                              <span className="fw-medium">{reservation.full_name}</span>
                              <br/>
                              <small className="text-muted">
                                {reservation.gender}, {reservation.age} years
                              </small>
                            </td>
                            <td>
                              <small>
                                <i className="fas fa-envelope text-muted me-1"></i> {reservation.email}
                                <br />
                                <i className="fas fa-phone text-muted me-1"></i> {reservation.phone}
                              </small>
                            </td>
                            <td>
                              <small>{reservation.goal}</small>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                ) : (
                  <div className="text-center p-4">
                    <i className="fas fa-calendar-xmark fa-3x mb-3 text-muted"></i>
                    <p>No reservations found. Your sessions will appear here when clients book with you.</p>
                  </div>
                )}
              </div>
            </div>
          )}
          
          {/* Availability Tab */}
          {activeTab === 'availability' && (
            <div className="card shadow-sm">
              <div className="card-header bg-dark text-white">
                <h5 className="mb-0">
                  <i className="fas fa-clock me-2"></i> My Availability
                </h5>
              </div>
              <div className="card-body">
                <div className="row">
                  <div className="col-md-6">
                    <div className="card mb-4">
                      <div className="card-header" style={{ backgroundColor: 'var(--highlight-color)', color: 'white' }}>
                        <h6 className="mb-0">Add New Availability</h6>
                      </div>
                      <div className="card-body">
                        <form onSubmit={handleAddAvailability}>
                          <div className="mb-3">
                            <label htmlFor="date" className="form-label">Date</label>
                            <input 
                              type="date" 
                              className="form-control" 
                              id="date" 
                              value={newAvailability.date}
                              onChange={(e) => setNewAvailability({...newAvailability, date: e.target.value})}
                              min={new Date().toISOString().split('T')[0]}
                              required
                            />
                          </div>
                          
                          <div className="row">
                            <div className="col-md-6 mb-3">
                              <label htmlFor="startTime" className="form-label">Start Time</label>
                              <input 
                                type="time" 
                                className="form-control" 
                                id="startTime" 
                                value={newAvailability.startTime}
                                onChange={(e) => setNewAvailability({...newAvailability, startTime: e.target.value})}
                                required
                              />
                            </div>
                            <div className="col-md-6 mb-3">
                              <label htmlFor="endTime" className="form-label">End Time</label>
                              <input 
                                type="time" 
                                className="form-control" 
                                id="endTime" 
                                value={newAvailability.endTime}
                                onChange={(e) => setNewAvailability({...newAvailability, endTime: e.target.value})}
                                min={newAvailability.startTime}
                                required
                              />
                            </div>
                          </div>
                          
                          <div className="d-grid">
                            <button type="submit" className="btn btn-primary">
                              <i className="fas fa-plus-circle me-1"></i> Add Slot
                            </button>
                          </div>
                        </form>
                      </div>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="card">
                      <div className="card-header" style={{ backgroundColor: 'var(--highlight-color)', color: 'white' }}>
                        <h6 className="mb-0">Current Availability</h6>
                      </div>
                      <div className="card-body">
                        {availability.length > 0 ? (
                          <div className="table-responsive" style={{ maxHeight: '300px', overflowY: 'auto' }}>
                            <table className="table table-sm table-hover">
                              <thead className="table-light">
                                <tr>
                                  <th>Date</th>
                                  <th>Time</th>
                                  <th>Status</th>
                                  <th>Action</th>
                                </tr>
                              </thead>
                              <tbody>
                                {availability.map(slot => (
                                  <tr key={slot.id}>
                                    <td>{formatDate(slot.date)}</td>
                                    <td>
                                      {formatTime(slot.start_time)} - {formatTime(slot.end_time)}
                                    </td>
                                    <td>
                                      {slot.is_booked ? (
                                        <span className="badge bg-danger">Booked</span>
                                      ) : (
                                        <span className="badge bg-success">Available</span>
                                      )}
                                    </td>
                                    <td>
                                      {!slot.is_booked && (
                                        <button 
                                          className="btn btn-sm btn-outline-danger"
                                          onClick={() => handleRemoveAvailability(slot.id)}
                                        >
                                          <i className="fas fa-trash-alt"></i>
                                        </button>
                                      )}
                                    </td>
                                  </tr>
                                ))}
                              </tbody>
                            </table>
                          </div>
                        ) : (
                          <div className="text-center p-4">
                            <i className="fas fa-calendar-plus fa-2x mb-3 text-muted"></i>
                            <p>No availability set. Add your available time slots to get bookings.</p>
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )}
          
          {/* Profile Tab */}
          {activeTab === 'profile' && (
            <div className="card shadow-sm">
              <div className="card-header bg-dark text-white">
                <h5 className="mb-0">
                  <i className="fas fa-user-cog me-2"></i> Profile Settings
                </h5>
              </div>
              <div className="card-body">
                <form onSubmit={handleUpdateProfile}>
                  <div className="row">
                    <div className="col-md-6 mb-3">
                      <label htmlFor="name" className="form-label">Name</label>
                      <input 
                        type="text" 
                        className="form-control" 
                        id="name" 
                        value={profile.name}
                        disabled
                      />
                      <small className="text-muted">Contact admin to change your name</small>
                    </div>
                    <div className="col-md-6 mb-3">
                      <label htmlFor="email" className="form-label">Email</label>
                      <input 
                        type="email" 
                        className="form-control" 
                        id="email" 
                        value={profile.email}
                        disabled
                      />
                      <small className="text-muted">Contact admin to change your email</small>
                    </div>
                  </div>
                  
                  <div className="mb-3">
                    <label htmlFor="specialty" className="form-label">Specialty</label>
                    <input 
                      type="text" 
                      className="form-control" 
                      id="specialty" 
                      value={profile.specialty}
                      onChange={(e) => setProfile({...profile, specialty: e.target.value})}
                      required
                    />
                  </div>
                  
                  <div className="mb-3">
                    <label htmlFor="bio" className="form-label">Bio</label>
                    <textarea 
                      className="form-control" 
                      id="bio" 
                      rows="4"
                      value={profile.bio || ''}
                      onChange={(e) => setProfile({...profile, bio: e.target.value})}
                    ></textarea>
                  </div>
                  
                  <div className="text-end">
                    <button type="submit" className="btn btn-primary">
                      <i className="fas fa-save me-1"></i> Update Profile
                    </button>
                  </div>
                </form>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default CoachDashboard;
