import React, { useState, useEffect } from 'react';

const ToastNotification = () => {
  const [toasts, setToasts] = useState([]);
  
  useEffect(() => {
    const handleToast = (event) => {
      const { message, type = 'success', duration = 5000, title } = event.detail;
      const id = Date.now();
      
      // Add new toast to the list
      setToasts(prev => [...prev, { id, message, type, title }]);
      
      // Remove toast after specified duration
      setTimeout(() => {
        setToasts(prev => prev.filter(toast => toast.id !== id));
      }, duration);
    };
    
    // Listen for custom toast events
    window.addEventListener('show-toast', handleToast);
    
    return () => {
      window.removeEventListener('show-toast', handleToast);
    };
  }, []);
  
  if (toasts.length === 0) return null;
  
  // Get toast background color based on type
  const getToastStyles = (type) => {
    switch (type) {
      case 'success':
        return {
          backgroundColor: '#4CAF50',
          borderLeft: '5px solid #2E7D32'
        };
      case 'error':
        return {
          backgroundColor: '#F44336',
          borderLeft: '5px solid #B71C1C'
        };
      case 'warning':
        return {
          backgroundColor: '#FF9800',
          borderLeft: '5px solid #E65100'
        };
      case 'info':
        return {          backgroundColor: '#1e293b',
          borderLeft: '5px solid var(--highlight-color)'
        };
      default:
        return {
          backgroundColor: '#4CAF50',
          borderLeft: '5px solid #2E7D32'
        };
    }
  };
  
  // Get toast icon based on type
  const getToastIcon = (type) => {
    switch (type) {
      case 'success':
        return 'fas fa-check-circle';
      case 'error':
        return 'fas fa-exclamation-circle';
      case 'warning':
        return 'fas fa-exclamation-triangle';
      case 'info':
        return 'fas fa-info-circle';
      default:
        return 'fas fa-bell';
    }
  };
  
  return (
    <div className="toast-container" style={{
      position: 'fixed',
      bottom: '20px',
      right: '20px',
      zIndex: 9999
    }}>
      {toasts.map((toast) => (
        <div 
          key={toast.id}
          className="toast show animate__animated animate__fadeIn"
          style={{
            ...getToastStyles(toast.type),
            color: 'white',
            padding: '15px 20px',
            marginTop: '10px',
            borderRadius: '4px',
            boxShadow: '0 4px 12px rgba(0,0,0,0.15)',
            minWidth: '300px',
            opacity: 1,
            transition: 'all 0.3s ease',
            transform: 'translateX(0)',
            animation: 'fadeIn 0.3s'
          }}
        >
          <div className="d-flex align-items-center">
            <div className="me-3">
              <i className={`${getToastIcon(toast.type)} fa-lg`}></i>
            </div>
            <div style={{ flex: 1 }}>
              {toast.title && (
                <div style={{ fontWeight: 'bold', marginBottom: '4px' }}>
                  {toast.title}
                </div>
              )}
              <div>{toast.message}</div>
            </div>
            <button 
              type="button" 
              className="btn-close ms-auto" 
              onClick={() => setToasts(prev => prev.filter(t => t.id !== toast.id))}
              style={{ color: 'white' }}
              aria-label="Close"
            ></button>
          </div>
        </div>
      ))}
    </div>
  );
};

// Helper function to show toast from anywhere in the app
export const showToast = (message, options = {}) => {
  const { type = 'success', duration = 5000, title } = options;
  const event = new CustomEvent('show-toast', { 
    detail: { message, type, duration, title } 
  });
  window.dispatchEvent(event);
};

export default ToastNotification;
