import React, { useState, useContext, useEffect } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { AuthContext } from '../context/AuthContext';

const Login = ({ redirectPath = '/admin' }) => {
  const [credentials, setCredentials] = useState({
    matricule: '',
    password: ''
  });
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();
  const { adminUser, login } = useContext(AuthContext);
  
  // Check if already logged in as admin
  useEffect(() => {
    if (adminUser) {
      navigate(redirectPath);
    }
  }, [adminUser, navigate, redirectPath]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setCredentials(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setLoading(true);
    
    try {
      console.log('Attempting admin login with:', credentials);
      
      try {
        // Use the login function from AuthContext which will set adminUser
        await login(credentials.matricule, credentials.password);
        console.log('Auth context updated, redirecting to:', redirectPath);
        navigate(redirectPath);
      } catch (serverError) {        // Log server error
        console.error('Server login failed:', serverError);
        
        // Display error message to user
        if (serverError.response?.data?.error) {
          setError(serverError.response.data.error);
        } else if (serverError.message === 'Network Error') {
          setError('Impossible de se connecter au serveur. Veuillez vrifier votre connexion internet et ressayer.');
        } else {
          setError('Une erreur est survenue lors de la connexion. Veuillez ressayer plus tard.');
        }
      }
    } catch (error) {
      console.error('Login process error:', error);
      setError('Login process failed. Please try again.');
    } finally {
      setLoading(false);
    }
  };
  return (
    <div className="container mt-5">
      <div className="row justify-content-center">
        <div className="col-md-6 col-lg-4">
          <div className="card shadow">
            <div className="card-body p-4">
              <h2 className="text-center mb-4">
                <i className="fas fa-user-shield me-2"></i>
                Admin Login Only
              </h2>
              
              {error && (
                <div className="alert alert-danger" role="alert">
                  <i className="fas fa-exclamation-triangle me-2"></i>
                  {error}
                </div>
              )}
              
              <form onSubmit={handleSubmit}>
                <div className="mb-3">
                  <label className="form-label">Matricule</label>
                  <div className="input-group">
                    <span className="input-group-text">
                      <i className="fas fa-id-card"></i>
                    </span>
                    <input
                      type="text"
                      className="form-control"
                      name="matricule"
                      value={credentials.matricule}
                      onChange={handleChange}
                      required
                      placeholder="Enter your matricule"
                    />
                  </div>
                </div>
                
                <div className="mb-3">
                  <label className="form-label">Password</label>
                  <div className="input-group">
                    <span className="input-group-text">
                      <i className="fas fa-lock"></i>
                    </span>
                    <input
                      type="password"
                      className="form-control"
                      name="password"
                      value={credentials.password}
                      onChange={handleChange}
                      required
                    />
                  </div>
                </div>
                
                <div className="d-grid gap-2 mt-4">
                  <button 
                    type="submit" 
                    className="btn btn-primary" 
                    disabled={loading}
                  >
                    {loading ? (
                      <>
                        <span className="spinner-border spinner-border-sm me-2" role="status" aria-hidden="true"></span>
                        Logging in...
                      </>
                    ) : (
                      <>
                        <i className="fas fa-sign-in-alt me-2"></i>
                        Login
                      </>
                    )}
                  </button>
                </div>
              </form>
              
              <div className="text-center mt-3">
                <a href="/" className="text-decoration-none">
                  <i className="fas fa-arrow-left me-1"></i>
                  Back to Home
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;
