import React, { useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate, useLocation } from 'react-router-dom';
import Navbar from './components/Navbar';
import Home from './pages/Home';
import Login from './pages/Login';
import AdminLogin from './pages/AdminLogin';
import ClientLogin from './pages/ClientLogin';
import CoachLogin from './pages/CoachLogin';
import Admin from './pages/Admin';
import CoachDashboard from './pages/CoachDashboard';
import ProtectedRoute from './components/ProtectedRoute';
import { AuthProvider, AuthContext } from './context/AuthContext';
import { ToastProvider } from './context/ToastContext';
import WebSocketClient from './components/WebSocketClient';
import ToastNotification from './components/ToastNotification';
import ConfirmDialog from './components/ConfirmDialog';

function App() {  return (
    <AuthProvider>
      <ToastProvider>
        <Router basename={process.env.REACT_APP_USE_BASENAME === 'true' ? '/coach-reservation' : '/'}>
          <div className="App">
            {/* WebSocket client for real-time updates */}
            <WebSocketClient />
              {/* Toast notifications for real-time messages */}
            <ToastNotification />
            
            {/* Custom confirmation dialogs */}
            <ConfirmDialog />
            
            <Routes>
              {/* Home route with client-facing content only */}
              <Route path="/" element={
                <>
                  <Navbar showAdminLink={false} />
                  <div className="container mt-4">
                    <Home />
                  </div>
                </>
              } />
              
              {/* Client login */}
              <Route path="/login" element={
                <>
                  <Navbar showAdminLink={false} />
                  <div className="container mt-4">
                    <ClientLogin />
                  </div>
                </>
              } />
              
              {/* Alternative client login path for backward compatibility */}
              <Route path="/client/login" element={<Navigate to="/login" />} />
                {/* Coach login */}
              <Route path="/coach/login" element={
                <>
                  <Navbar showAdminLink={false} />
                  <div className="container mt-4">
                    <CoachLogin />
                  </div>
                </>
              } />              {/* Coach login */}
              <Route path="/coach/login" element={
                <>
                  <Navbar showAdminLink={false} showCoachLink={true} />
                  <div className="container mt-4">
                    <CoachLogin />
                  </div>
                </>
              } />
              
              {/* Coach dashboard - exact path */}
              <Route path="/coach/" element={
                <>
                  <Navbar showAdminLink={false} showCoachLink={true} />
                  <ProtectedRoute requiredRole="coach">
                    <CoachDashboard />
                  </ProtectedRoute>
                </>
              } />
              
              {/* Coach dashboard - wildcard for sub-routes */}
              <Route path="/coach/*" element={
                <>
                  <Navbar showAdminLink={false} showCoachLink={true} />
                  <ProtectedRoute requiredRole="coach">
                    <CoachDashboard />
                  </ProtectedRoute>
                </>
              } />
              
              {/* Catch /coach without trailing slash and redirect */}
              <Route path="/coach" element={<Navigate to="/coach/" replace />} />
              
              {/* Admin route with authentication */}
              <Route path="/admin" element={
                <>
                  <Navbar showAdminLink={true} />
                  <div className="container mt-4">
                    <ProtectedRoute requiredRole="admin">
                      <Admin />
                    </ProtectedRoute>
                  </div>
                </>
              } />
              
              {/* Admin login */}
              <Route path="/admin/login" element={
                <>
                  <Navbar showAdminLink={true} />
                  <div className="container mt-4">
                    <AdminLogin />
                  </div>
                </>
              } />
            </Routes>
          </div>
        </Router>
      </ToastProvider>
    </AuthProvider>
  );
}

export default App;
