import React, { useState, useContext, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { AuthContext } from '../context/AuthContext';

const ClientLogin = ({ redirectPath = '/' }) => {
  const [credentials, setCredentials] = useState({
    email: '',
    password: ''
  });
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();
  const { currentUser, clientLogin } = useContext(AuthContext);
  
  // Check if already logged in
  useEffect(() => {
    if (currentUser) {
      navigate(redirectPath);
    }
  }, [currentUser, navigate, redirectPath]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setCredentials(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setLoading(true);
    
    try {
      console.log('Attempting client login with:', credentials);
      
      try {
        // Use the clientLogin function from AuthContext which will set currentUser
        const user = await clientLogin(credentials.email, credentials.password);
        
        // Check if the user has the 'user' role
        if (user.role !== 'user') {
          setError('This login is for clients only. Please use the appropriate login.');
          setLoading(false);
          return;
        }
        
        // Redirect after auth context is updated
        console.log('Auth context updated, redirecting to:', redirectPath);
        navigate(redirectPath);
        
      } catch (serverError) {
        console.error('Server login failed:', serverError);
        
        // If server is down or connection refused
        const isServerDown = 
          serverError.message?.includes('ECONNREFUSED') || 
          serverError.message === 'Network Error' ||
          serverError.code === 'ECONNABORTED';
        
        if (isServerDown) {
          setError('Server is unavailable. Please try again later.');
        } else {
          setError(serverError.response?.data?.error || 'Invalid credentials');
        }
      }
    } catch (error) {
      console.error('Login process error:', error);
      setError('Login process failed. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="container mt-5">
      <div className="row justify-content-center">
        <div className="col-md-6 col-lg-4">          <div className="card shadow-lg" style={{ 
              border: '1px solid rgba(230, 166, 76, 0.3)', 
              borderRadius: 'var(--border-radius-lg)',
              animation: 'fadeIn 0.6s ease-out',
              transform: 'translateY(0)',
              transition: 'all 0.3s ease'
            }}>
            <div className="card-body p-4">
              <h2 className="text-center mb-4">
                <i className="fas fa-user me-2"></i>
                Client Login
              </h2>
              
              {error && (
                <div className="alert alert-danger" role="alert">
                  <i className="fas fa-exclamation-triangle me-2"></i>
                  {error}
                </div>
              )}
              
              <form onSubmit={handleSubmit}>
                <div className="mb-3">
                  <label className="form-label">Email</label>
                  <div className="input-group">
                    <span className="input-group-text">
                      <i className="fas fa-envelope"></i>
                    </span>
                    <input
                      type="email"
                      className="form-control"
                      name="email"
                      value={credentials.email}
                      onChange={handleChange}
                      required
                      placeholder="Enter your email"
                    />
                  </div>
                </div>
                
                <div className="mb-3">
                  <label className="form-label">Password</label>
                  <div className="input-group">
                    <span className="input-group-text">
                      <i className="fas fa-lock"></i>
                    </span>
                    <input
                      type="password"
                      className="form-control"
                      name="password"
                      value={credentials.password}
                      onChange={handleChange}
                      required
                    />
                  </div>
                </div>
                  <div className="d-grid gap-2 mt-4">
                  <button 
                    type="submit" 
                    className="btn btn-primary py-2" 
                    disabled={loading}
                    style={{ fontSize: '1.05rem' }}
                  >
                    {loading ? (
                      <>
                        <span className="spinner-border spinner-border-sm me-2" role="status" aria-hidden="true"></span>
                        Logging in...
                      </>
                    ) : (
                      <>
                        <i className="fas fa-sign-in-alt me-2"></i>
                        Login to Client Area
                      </>
                    )}
                  </button>
                </div>
              </form>
                <div className="text-center mt-4">
                <a href="/" className="text-decoration-none" style={{ color: 'var(--highlight-color)', fontWeight: '500' }}>
                  <i className="fas fa-arrow-left me-2"></i>
                  Back to Home
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ClientLogin;
