import React, { useEffect, useContext, useRef } from 'react';
import { AuthContext } from '../context/AuthContext';
import { showToast } from './ToastNotification';

const WebSocketClient = () => {
  const { currentUser, updatePoints } = useContext(AuthContext);
  const wsRef = useRef(null);
  
  useEffect(() => {
    let ws = null;
      // Only establish WebSocket connection if user is logged in
    if (currentUser && currentUser.id) {
      const protocol = window.location.protocol === 'https:' ? 'wss:' : 'ws:';
      
      // Use environment variable for production
      let wsUrl;
      if (process.env.NODE_ENV === 'production') {
        const apiUrl = process.env.REACT_APP_API_URL || '';
        const apiHost = apiUrl.replace(/^https?:\/\//, '');
        wsUrl = `${protocol}//${apiHost}/ws`;
      } else {
        const host = window.location.hostname;
        const port = '5000';
        wsUrl = `${protocol}//${host}:${port}/ws`;
      }
      
      console.log(`Connecting to WebSocket at: ${wsUrl}`);
      
      ws = new WebSocket(wsUrl);
      wsRef.current = ws;
      
      ws.onopen = () => {
        console.log('WebSocket connection established');
        
        // Authenticate the WebSocket connection
        ws.send(JSON.stringify({
          type: 'auth',
          userId: currentUser.id
        }));
      };
      
      ws.onmessage = (event) => {
        try {
          const data = JSON.parse(event.data);
          console.log('WebSocket message received:', data);
          
          // Handle different message types
          switch (data.type) {
            case 'auth_success':
              console.log('WebSocket authentication successful');
              break;
              
            case 'points_updated':
              // Update points in context and show notification
              if (data.points !== undefined && updatePoints) {
                // Create a user object with updated points
                const updatedUser = {
                  ...currentUser,
                  points: data.points
                };
                
                // Update the context
                updatePoints(updatedUser);
                
                // Show a notification using our enhanced toast component
                showToast(
                  data.message || `Your points have been updated. New balance: ${data.points} points.`,
                  { 
                    type: 'info',
                    title: 'Points Updated',
                    duration: 7000
                  }
                );
              }
              break;
              
            default:
              console.log(`Unhandled WebSocket message type: ${data.type}`);
          }
        } catch (error) {
          console.error('Error parsing WebSocket message:', error);
        }
      };
      
      ws.onerror = (error) => {
        console.error('WebSocket error:', error);
      };
      
      ws.onclose = (event) => {
        console.log(`WebSocket connection closed: ${event.code} ${event.reason}`);
        // Try to reconnect after a delay
        setTimeout(() => {
          if (currentUser && currentUser.id) {
            console.log('Attempting to reconnect WebSocket...');
            // The component will re-render and useEffect will run again
          }
        }, 5000);
      };
    }
    
    // Cleanup function to close WebSocket when component unmounts
    return () => {
      if (ws) {
        console.log('Closing WebSocket connection');
        ws.close();
      }
    };
  }, [currentUser, updatePoints]);
  
  // Function to show browser notifications and toast
  const showNotification = (message) => {
    // Check if browser supports notifications
    if (!("Notification" in window)) {
      console.log("Browser does not support desktop notifications");
      // Fall back to toast only
      showToast(message, { type: 'info' });
      return;
    }
    
    // Check if permission is already granted
    if (Notification.permission === "granted") {
      new Notification("Coach Reservation System", { 
        body: message,
        icon: '/logo.png'
      });
    } 
    // Otherwise, request permission
    else if (Notification.permission !== "denied") {
      Notification.requestPermission().then(permission => {
        if (permission === "granted") {
          new Notification("Coach Reservation System", { 
            body: message,
            icon: '/logo.png'
          });
        }
      });
    }
    
    // Always show toast notification
    showToast(message, { type: 'info' });
  };
  
  // This component doesn't render anything
  return null;
};

export default WebSocketClient;
