import React, { useState } from 'react';
import axios from 'axios';
import { showToast } from './ToastNotification';

const ImageUploader = ({ onImageUploaded, existingImage = null }) => {
  const [selectedFile, setSelectedFile] = useState(null);
  const [preview, setPreview] = useState(existingImage);  const [isUploading, setIsUploading] = useState(false);
  const [uploadError, setUploadError] = useState(null);
  const [uploadProgress, setUploadProgress] = useState(0);

  const handleFileChange = (event) => {
    const file = event.target.files[0];
    
    if (file) {
      setSelectedFile(file);
      
      // Create a preview URL
      const reader = new FileReader();
      reader.onload = () => {
        setPreview(reader.result);
      };
      reader.readAsDataURL(file);
      
      // Reset states
      setUploadError(null);
      setUploadProgress(0);
      
      // Automatically trigger upload after file selection
      setTimeout(() => {
        handleUpload(file);
      }, 100);
    }
  };
  const handleUpload = async (fileToUpload = null) => {    const fileForUpload = fileToUpload || selectedFile;
    
    if (!fileForUpload) {
      const errorMsg = 'Please select an image to upload';
      setUploadError(errorMsg);
      showToast(errorMsg, { type: 'warning', title: 'No Image Selected' });
      return;
    }
    
    setIsUploading(true);
    setUploadError(null);
    setUploadProgress(10);
    
    // Create form data
    const formData = new FormData();
    formData.append('image', fileForUpload);    try {
      setUploadProgress(30);      
      
      // Upload through our server proxy endpoint instead of directly to ImgBB
      const response = await axios.post('/api/uploads/image', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        },
        onUploadProgress: (progressEvent) => {
          const percentCompleted = Math.round((progressEvent.loaded * 70) / progressEvent.total);
          setUploadProgress(30 + percentCompleted); // Starting from 30% (after preparation)
        }
      });
      
      setUploadProgress(100);
      
      if (response.data.success) {        const imageUrl = response.data.data.url;
        console.log('Image successfully uploaded to ImgBB:', imageUrl);
        
        // Pass the URL back to the parent component
        onImageUploaded(imageUrl);
        
        // Show success toast
        showToast('Image uploaded successfully!', { 
          type: 'success', 
          title: 'Upload Complete' 
        });
        
        setTimeout(() => {
          setIsUploading(false);
        }, 500);} else {
        console.error('ImgBB API returned success:false', response.data);
        const errorMsg = 'Image upload failed. Please try again.';
        setUploadError(errorMsg);
        showToast(errorMsg, { type: 'error', title: 'Upload Failed' });
        setIsUploading(false);
      }
    } catch (error) {
      console.error('Error uploading image to ImgBB:', error);
      console.error('Error details:', error.response?.data || error.message);
      
      let errorMsg = 'Failed to upload image. Please try again.';
      if (error.message === 'Network Error') {
        errorMsg = 'Network error. Please check your internet connection and try again.';
      } else if (error.response) {
        errorMsg = `Upload failed: ${error.response.data?.error?.message || 'Server error'}`;
      }
      
      setUploadError(errorMsg);
      showToast(errorMsg, { 
        type: 'error', 
        title: 'Image Upload Error', 
        duration: 7000 
      });
      setIsUploading(false);
    }
  };

  const handleRemoveImage = () => {
    setSelectedFile(null);
    setPreview(null);
    onImageUploaded('');
  };

  return (
    <div className="image-uploader">
      <div className="mb-3">
        {preview ? (
          <div className="text-center mb-3">
            <img 
              src={preview} 
              alt="Preview" 
              className="img-thumbnail" 
              style={{ maxHeight: '200px', maxWidth: '100%' }} 
            />
          </div>
        ) : (
          <div className="image-placeholder mb-3 text-center p-4 border rounded bg-light">
            <i className="fas fa-image fa-3x text-muted mb-2"></i>
            <p className="text-muted mb-0">No image selected</p>
          </div>
        )}
        
        <div className="d-flex gap-2">          <div className="input-group">
            <input 
              type="file" 
              className="form-control"
              id="imageInput"
              accept="image/*"
              onChange={handleFileChange}
              disabled={isUploading}
            />
            
            {preview && (
              <button 
                type="button" 
                className="btn btn-outline-danger"
                onClick={handleRemoveImage}
                disabled={isUploading}
              >
                <i className="fas fa-trash me-1"></i> Remove
              </button>
            )}
          </div>
        </div>
        
        {isUploading && (
          <div className="mt-2">
            <div className="progress">
              <div 
                className="progress-bar progress-bar-striped progress-bar-animated" 
                role="progressbar" 
                style={{ width: `${uploadProgress}%` }}
                aria-valuenow={uploadProgress} 
                aria-valuemin="0" 
                aria-valuemax="100"
              ></div>
            </div>
            <small className="text-muted">{uploadProgress}% complete</small>
          </div>
        )}
        
        {uploadError && (
          <div className="alert alert-danger mt-2">
            <i className="fas fa-exclamation-triangle me-2"></i>
            {uploadError}
          </div>
        )}
        
        <small className="text-muted d-block mt-2">
          Supported formats: JPG, PNG, GIF. Max size: 10MB
        </small>
      </div>
    </div>
  );
};

export default ImageUploader;
