import React, { useEffect, useState, useContext } from 'react';
import { Navigate, useNavigate } from 'react-router-dom';
import axios from 'axios';
import { AuthContext } from '../context/AuthContext';

const ProtectedRoute = ({ children, requiredRole = 'admin' }) => {
  const { currentUser, adminUser, coachUser, isAdmin, isCoach } = useContext(AuthContext);
  const [loading, setLoading] = useState(true);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const navigate = useNavigate();
  
  useEffect(() => {
    const verifyAuth = async () => {
      // For admin routes
      if (requiredRole === 'admin') {
        if (adminUser && isAdmin) {
          console.log('User is already authenticated as admin');
          setIsAuthenticated(true);
          setLoading(false);
          return;
        }
        
        if (!adminUser) {
          console.log('No admin user found, redirecting to admin login');
          setIsAuthenticated(false);
          setLoading(false);
          return;
        }
      }      // For coach routes
      if (requiredRole === 'coach') {
        // Check if we have a coach user in context
        if (coachUser && coachUser.role === 'coach') {
          console.log('User is already authenticated as coach');
          setIsAuthenticated(true);
          setLoading(false);
          return;
        }
        
        const coachToken = localStorage.getItem('coachToken');
        const storedCoachUser = JSON.parse(localStorage.getItem('coachUser') || 'null');
        
        if (!coachToken || !storedCoachUser) {
          console.log('No coach token or user found, redirecting to coach login');
          setIsAuthenticated(false);
          setLoading(false);
          return;
        }
        
        try {
          // Set the authorization header
          axios.defaults.headers.common['Authorization'] = `Bearer ${coachToken}`;
          
          // Verify token
          await axios.get('/api/auth/me');
          setIsAuthenticated(true);
          setLoading(false);
          return;
        } catch (error) {
          console.error('Coach token verification failed:', error);
          localStorage.removeItem('coachToken');
          localStorage.removeItem('coachUser');
          setIsAuthenticated(false);
          setLoading(false);
          return;
        }
      }try {
        let token;
        
        if (requiredRole === 'admin') {
          token = localStorage.getItem('adminToken');
          if (!token) {
            throw new Error('No admin token found');
          }
          // Set the authorization header
          axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
          
          // Verify token
          await axios.get('/api/auth/me');
          setIsAuthenticated(true);
        }
      } catch (error) {
        console.error(`${requiredRole} authentication verification failed:`, error);
        
        if (requiredRole === 'admin') {
          localStorage.removeItem('adminToken');
          localStorage.removeItem('adminUser');
        }
        
        setIsAuthenticated(false);
      } finally {
        setLoading(false);
      }
    };

    verifyAuth();
  }, [adminUser, isAdmin, requiredRole, navigate]);
  if (loading) {
    return (
      <div className="container mt-5 text-center">
        <div className="spinner-border" role="status">
          <span className="visually-hidden">Loading...</span>
        </div>
        <p className="mt-3">Verifying authentication...</p>
      </div>
    );
  }
    // Route to appropriate login page based on required role
  if (!isAuthenticated) {
    if (requiredRole === 'admin') {
      return <Navigate to="/admin/login" replace />;
    } else if (requiredRole === 'coach') {
      return <Navigate to="/coach/login" replace />;
    } else {
      return <Navigate to="/login" replace />;
    }
  }
  
  return children;
};

export default ProtectedRoute;
