import React, { useState, useContext, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { AuthContext } from '../context/AuthContext';
import axios from 'axios';

const CoachLogin = () => {
  const [matricule, setMatricule] = useState('');
  const [password, setPassword] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  
  const { coachUser, setCurrentUser, setCoachUser } = useContext(AuthContext);
  const navigate = useNavigate();
  
  // Redirect to coach dashboard if already logged in
  useEffect(() => {
    const coachToken = localStorage.getItem('coachToken');
    const coachUserData = JSON.parse(localStorage.getItem('coachUser') || 'null');
    
    if (coachToken && coachUserData) {
      navigate('/coach', { replace: true });
    }
  }, [navigate, coachUser]);
    const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!matricule || !password) {
      setError('Matricule and password are required');
      return;
    }
    
    try {
      setLoading(true);
      setError('');
      
      const response = await axios.post('/api/auth/coach-login', { matricule, password });
      const { token, ...userData } = response.data;
      
      // Store token in local storage
      localStorage.setItem('coachToken', token);
      localStorage.setItem('coachUser', JSON.stringify({...userData, role: 'coach'}));
      
      // Explicitly remove client token/user to enforce separation
      localStorage.removeItem('clientToken');
      localStorage.removeItem('clientUser');
      
      // Set Authorization header for future requests
      delete axios.defaults.headers.common['Authorization'];
      axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
      
      // Update both context states for coach
      const coachData = {...userData, role: 'coach'};
      setCoachUser(coachData);
      
      // Only set currentUser for coach paths
      if (window.location.pathname.startsWith('/coach')) {
        setCurrentUser(coachData);
      } else {
        setCurrentUser(null);
      }
      
      // Redirect to coach dashboard
      navigate('/coach', { replace: true });
    } catch (error) {
      console.error('Login error:', error);
      setError(error.response?.data?.error || 'Login failed. Please check your credentials.');
    } finally {
      setLoading(false);
    }
  };
  
  return (
    <div className="container mt-5">
      <div className="row justify-content-center">
        <div className="col-md-6 col-lg-5">
          <div className="card shadow border-0">
            <div className="card-body p-4 p-md-5">
              <div className="text-center mb-4">
                <div className="d-inline-block mb-3">
                  <img src="/logo_white.png" alt="Logo" height="60" className="bg-dark p-2 rounded" />
                </div>
                <h2 className="fw-bold" style={{color: 'var(--highlight-color)'}}><i className="fas fa-user-tie me-2"></i>Coach Login</h2>
              </div>
              
              {error && (
                <div className="alert alert-danger" role="alert">
                  {error}
                </div>
              )}
              
              <form onSubmit={handleSubmit}>
                <div className="mb-3">
                  <label htmlFor="matricule" className="form-label fw-bold">Matricule</label>
                  <div className="input-group">
                    <span className="input-group-text"><i className="fas fa-id-badge"></i></span>
                    <input 
                      type="text" 
                      className="form-control" 
                      id="matricule"
                      value={matricule}
                      onChange={(e) => setMatricule(e.target.value)}
                      placeholder="Enter your matricule"
                      required
                    />
                  </div>
                </div>
                
                <div className="mb-4">
                  <div className="d-flex justify-content-between">
                    <label htmlFor="password" className="form-label fw-bold">Password</label>
                  </div>
                  <div className="input-group">
                    <span className="input-group-text"><i className="fas fa-lock"></i></span>
                    <input 
                      type="password" 
                      className="form-control" 
                      id="password"
                      value={password}
                      onChange={(e) => setPassword(e.target.value)}
                      placeholder="Enter your password"
                      required
                    />
                  </div>
                </div>
                  <div className="d-grid gap-2 mt-4">
                  <button 
                    type="submit" 
                    className="btn btn-primary py-2" 
                    disabled={loading}
                    style={{ fontSize: '1.05rem' }}
                  >
                    {loading ? (
                      <>
                        <span className="spinner-border spinner-border-sm me-2" role="status" aria-hidden="true"></span>
                        Logging in...
                      </>
                    ) : (
                      <>
                        <i className="fas fa-sign-in-alt me-2"></i>
                        Login to Coach Area
                      </>
                    )}
                  </button>
                </div>
              </form>
                <div className="text-center mt-4">
                <Link to="/" className="btn btn-link text-decoration-none" style={{color: 'var(--highlight-color)'}}>
                  <i className="fas fa-arrow-left me-1"></i> Back to Home
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CoachLogin;
